.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.primary_background {
  background: linear-gradient(-45deg, #171717, #121212, #101010, #0f0f0f);
  background-size: 400% 400%;
  animation: gradient 30s ease infinite;
  height: 100%;
}


.secondary_bg {
  background: linear-gradient(-45deg, #4C13A1, #2E0C61, #681AE0, #5E18C7);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  padding-left: 50px;
  padding-right:50px;
  padding-bottom: 50px;
  border-radius: 10px;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}
